#topHead ul a {
  display: inline-block;
  padding: 10px;
  font-size: medium;
  color: white;
  text-decoration: none;
  position: relative;
}

#topHead ul a::after {
  position: absolute;
  left: 0;
  bottom: 0;
  background: #000088;
  width: 0%;
  height: 5px;
  content: "";
  transition: 0.5s;
}
#topHead ul a:hover::after {
  width: 100%;
}

